.cardBtm-container {
  margin-top: 10px;
}

.cardBtm .cardBtm-box {
  margin: 0 5px 30px 5px;
  border-radius: 10px;
}
.cardBtm .cardBtm-text {
  padding: 0 10px;
}
.cardBtm .cardBtm-images {
  width: 320px;
  height: 220px;
  margin-left: 10px;
  position: relative;
}

.cardBtm-date {
  display: flex;
  gap: 5px;
}

.cardBtm img {
  width: 310px;
  height: 200px;
  object-fit: fill;
  border-radius: 10px;
}
.cardBtm h1 {
  font-size: 1rem;
  word-break: break-all;
  word-wrap: break-word;
}

.cardBtm p {
  font-size: 0.9rem;
  word-break: break-all;
  word-wrap: break-word;
}

.cardBtm-desc {
  font-size: 18px;
  margin: 10px 0;
}

.cardBtm-shadow {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.flex, .cardBtm-flexSB {
  display: flex;
}

.cardBtm-flexSB {
  align-items: center;
  justify-content: space-between;
}

.cardBtm-catagory .cardBtm-catagory1 {
  position: relative;
  margin-bottom: 20px;
}

.cardBtm-catagory {
  color: #fff;
  border-radius: 10px 10px 10px 0;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 2px;
  padding: 5px 10px;
  text-transform: uppercase;
}

.cardBtm-catagory1 {
  background: linear-gradient(90deg,#045f22 50%,#e00909 0);
  background-position: 100% 100%;
  background-size: 200% 100%;
  border-left: 1px solid #1aaaaa;
  cursor: pointer;
  display: block;
  left: 0;
  position: absolute;
  top: 0;
  transition: all .5s ease-out;
  width: auto;
}

.cardBtm-catagory1:hover {
  background-position: 0 100%;
}

.cardBtm-catagory1 span {
  color: #fff;
  letter-spacing: 0;
  text-transform: capitalize;
}

@media screen and (max-width: 600px) {
  .cardBtm-items {
    overflow: hidden;
  }
  .cardBtm-flexSB {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .cardBtm-images {
    min-width: 90%;
    max-width: fit-content;
    max-height: min-content;
    position: relative;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .cardBtm-img {
    width: 100%;
    height: 100%;
  }
  .cardBtm-images .cardBtm-img img {
    max-width: 100%;
    min-width: 100%;
    max-height: 100%;
    min-height: 100%;
    object-fit: fill;
    border-radius: 10px;
  }
}