.login-container {
    display: flex;
    justify-content: center;
    background-color: #0b1813;
}

.login-form {
    margin: 20px 0px;
    width: 400px;
    background-color: rgba(255,255,255,0.13);
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255,255,255,0.1);
    box-shadow: 0 0 40px rgba(8,7,16,0.6);
    padding: 20px 35px;
}
.login-form *{
    font-family: 'Poppins',sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}
.login-form h3{
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

.login-form label{
    display: block;
    margin-top: 20px;
    font-size: 16px;
    font-weight: 500;
}
.login-form input{
    display: block;
    height: 50px;
    width: 95%;
    background-color: rgba(252, 249, 249, 0.13);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 4px;
    font-size: 14px;
    font-weight: 300;
}
::placeholder{
    color: #e5e5e5;
}
.login-btn{
    margin-top: 50px;
    width: 100%;
    background-color: #1264cf;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.login-btn:hover {
    background-color: rgb(62, 62, 180);
}
.social{
  margin-top: 30px;
  display: flex;
}
.social div{
  border-radius: 3px;
  padding: 5px 10px 10px 5px;
  color: #eaf0fb;
  text-align: center;
}

@media only screen and (max-width: 550px) {
    .login-form {
        width: 100%;
        margin: 20px;
    }
}