.footer-container {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    background: rgb(5, 31, 41);
    color: white;
}

.footer-top-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 8px;
}

.footer-top {
    display: flex;
    flex-direction: column;
}

.footer-top-boxes {
    display: flex;
    flex-direction: column;
    padding: 5px;
    gap: 10px;
}

.footer-buttom-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

 /* inside */
 /* footer top  */
 .footer-info-logo {
     display: flex;
     justify-content: flex-start;
     align-items: center;
     gap: 5px;
 }

 .footer-info-logo h3 {
     font-size: 1rem;
     font-weight: normal;
     font-family: Arial, Helvetica, sans-serif;
 }
 
 .footer-info-details p {
    font-size: 1em;
    font-weight: normal;
    font-family: Georgia, 'Times New Roman', Times, serif;
 }
 
 .footer-contact {
     display: flex;
     justify-content: flex-start;
     align-items: center;
     gap: 5px;
     word-break: break-all;
 }
 /* ---- */

.footer-top-boxes a{
    text-decoration: none;
    color: white;
}

.footer-item {
    border-radius: 5px;
    background-color: rgba(112, 112, 112, 0.5);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .13), 0 1px 1px 0 rgba(0, 0, 0, .11);
    padding: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.footer-top-boxes ul {
    margin: 0;
    padding: 0;
}

.footer-top-boxes ul li {
    margin-bottom: 30px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.3);
    padding-bottom: 5px;
    display: flex;
    justify-content: space-between;
    position: relative;
}

.blank-line{
    margin-top: 10px;
    width: 100%;
    height: 2px;
    background-color: rgb(58, 54, 54);
}

@media only screen and (max-width: 1250px) {
    .footer-top-container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 8px;
    }
}

@media only screen and (max-width: 650px) {
    .footer-top-container {
        display: grid;
        grid-template-columns: 1fr;
        gap: 8px;
    }
}

@media only screen and (max-width: 280px) {
    .footer-item {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .footer-item img {
        min-width: 100%;
        min-height: 100px;
        object-fit: fill;
    }
    .footer-item p {
        width: 100%;
        height: auto;
    }
}
